body {
  background-color: black;
  background-repeat: no-repeat;
  font-size: 36px;
  color: pink;
}

p {
  color: #b0245b;
}

img {
  height: 200px;
}

.carouselImage {
  display: flex;
  height: 500px;
  align-items: center;
  text-align: center;
  margin-right: auto;
  margin-left: auto;
}

h1,
h2,
h3 {
  font-size: 72px;
  background: -webkit-linear-gradient(#ffabc8, #b0245b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

h4 {
  font-size: 36px;
  background: -webkit-linear-gradient(#ffabc8, #b0245b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.genre {
  border: 5px solid black;
  margin-left: auto;
  margin-right: auto;
  background-color: #b0245b;
}

.stackImage {
  height: 150px;
}

td {
  vertical-align: top;
  text-align: center;
}

.poem {
  font-size: 24px;
}